import Upload from '../Upload'
export default {
  model: {
    prop: 'visible',
    event: 'closeWindow'
  },
  props: ['visible'],
  methods: {
    closeWindow () {
      this.$emit('closeWindow', false)
    },
    onPreview (img) {
      this.$emit('preview', img)
    }
  },
  components: {
    Upload
  }

}



